/**
*?  Base
**/
@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
   ========================================================================== */
/**
 * Remove the margin in all browsers.
 */
body {
  margin: 0;
}

/**
 * Render the `main` element consistently in IE.
 */
main {
  display: block;
}

/**
 * Correct the font size and margin on `h1` elements within `section` and
 * `article` contexts in Chrome, Firefox, and Safari.
 */
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
   ========================================================================== */
/**
 * 1. Add the correct box sizing in Firefox.
 * 2. Show the overflow in Edge and IE.
 */
hr {
  box-sizing: content-box; /* 1 */
  height: 0; /* 1 */
  overflow: visible; /* 2 */
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
   ========================================================================== */
/**
 * Remove the gray background on active links in IE 10.
 */
a {
  background-color: transparent;
}

/**
 * 1. Remove the bottom border in Chrome 57-
 * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.
 */
abbr[title] {
  border-bottom: none; /* 1 */
  text-decoration: underline; /* 2 */
  text-decoration: underline dotted; /* 2 */
}

/**
 * Add the correct font weight in Chrome, Edge, and Safari.
 */
b,
strong {
  font-weight: bolder;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
 * Add the correct font size in all browsers.
 */
small {
  font-size: 80%;
}

/**
 * Prevent `sub` and `sup` elements from affecting the line height in
 * all browsers.
 */
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/* Embedded content
   ========================================================================== */
/**
 * Remove the border on images inside links in IE 10.
 */
img {
  border-style: none;
}

/* Forms
   ========================================================================== */
/**
 * 1. Change the font styles in all browsers.
 * 2. Remove the margin in Firefox and Safari.
 */
button,
input,
optgroup,
select,
textarea {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
}

/**
 * Show the overflow in IE.
 * 1. Show the overflow in Edge.
 */
button,
input { /* 1 */
  overflow: visible;
}

/**
 * Remove the inheritance of text transform in Edge, Firefox, and IE.
 * 1. Remove the inheritance of text transform in Firefox.
 */
button,
select { /* 1 */
  text-transform: none;
}

/**
 * Correct the inability to style clickable types in iOS and Safari.
 */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/**
 * Remove the inner border and padding in Firefox.
 */
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
 * Restore the focus styles unset by the previous rule.
 */
button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
 * Correct the padding in Firefox.
 */
fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
 * 1. Correct the text wrapping in Edge and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 * 3. Remove the padding so developers are not caught out when they zero out
 *    `fieldset` elements in all browsers.
 */
legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  padding: 0; /* 3 */
  white-space: normal; /* 1 */
}

/**
 * Add the correct vertical alignment in Chrome, Firefox, and Opera.
 */
progress {
  vertical-align: baseline;
}

/**
 * Remove the default vertical scrollbar in IE 10+.
 */
textarea {
  overflow: auto;
}

/**
 * 1. Add the correct box sizing in IE 10.
 * 2. Remove the padding in IE 10.
 */
[type=checkbox],
[type=radio] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
}

/**
 * Correct the cursor style of increment and decrement buttons in Chrome.
 */
[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

/**
 * 1. Correct the odd appearance in Chrome and Safari.
 * 2. Correct the outline style in Safari.
 */
[type=search] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
 * Remove the inner padding in Chrome and Safari on macOS.
 */
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style clickable types in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */
::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/* Interactive
   ========================================================================== */
/*
 * Add the correct display in Edge, IE 10+, and Firefox.
 */
details {
  display: block;
}

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

/* Misc
   ========================================================================== */
/**
 * Add the correct display in IE 10+.
 */
template {
  display: none;
}

/**
 * Add the correct display in IE 10.
 */
[hidden] {
  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.content-center {
  max-width: 80rem;
}

.to-right {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.rounded {
  border-radius: 0.5rem;
}

.rounded-top {
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
}

.btn-yellow {
  background-color: #e08709;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.btn-yellow:hover {
  background-color: #c77808;
  cursor: pointer;
}

.btn-yellow-block {
  background-color: #e08709;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.btn-yellow-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.btn-green {
  background-color: #71b100;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.btn-green:hover {
  background-color: #619800;
  cursor: pointer;
}

.btn-red-block {
  background-color: #a50000;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
}
.btn-red-block:hover {
  background-color: #8c0000;
  cursor: pointer;
}

.btn-green-block {
  background-color: #71b100;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.btn-green-block:hover {
  background-color: #619800;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #ffffff;
}
.dark-mode a,
.dark-mode h1,
.dark-mode h2,
.dark-mode h3,
.dark-mode h4,
.dark-mode h5,
.dark-mode h6 {
  color: #e1e1e1;
}
.dark-mode .anuncio,
.dark-mode .summary-property {
  background-color: #333333;
  border: 1px solid #e1e1e1;
}
.dark-mode .anuncio .price,
.dark-mode .summary-property .price {
  color: #71b100;
}
.dark-mode .anuncio .anuncio-icon,
.dark-mode .summary-property .anuncio-icon {
  filter: invert(100%);
}
.dark-mode .form label,
.dark-mode .form legend,
.dark-mode .form p {
  color: #e1e1e1;
}
.dark-mode .form input:not([type=submit]),
.dark-mode .form textarea,
.dark-mode .form select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #ffffff;
}
.dark-mode table.propiedades thead {
  background-color: #71b100;
}
.dark-mode table.propiedades thead th {
  color: #ffffff;
  padding: 2rem;
}
.dark-mode table.propiedades tbody {
  color: #ffffff;
}

/**
*?  UI
**/
.no-webp .init {
  background-image: url("../img/header.jpg");
}

.webp .init {
  background-image: url("../img/header.webp");
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.init {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.init {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.init .container-header {
    min-height: 70rem;
  }
}

.container-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.container-header h1 {
  text-align: left;
  color: #ffffff;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.right {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .right {
    align-items: flex-end;
  }
}
.right .dark-mode-btn {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.right .dark-mode-btn:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .right .dark-mode-btn {
    margin-top: 0;
  }
}

.navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .navbar {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navigation {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navigation {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navigation a {
  color: #ffffff;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
.navigation a:hover {
  color: #71b100;
}
@media (min-width: 768px) {
  .navigation a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navigation a:last-of-type {
    margin-right: 0;
  }
}

/**
*? Menu de Hamburguesa
**/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.show {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .icons-nosotros {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.icon {
  text-align: center;
}
.icon img {
  height: 10rem;
}
.icon h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .anuncios-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.anuncio {
  background-color: #eeeeee;
  border: 1px solid #fbfbfb;
  margin-top: 2rem;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .anuncio {
    margin-top: 0;
  }
}
.anuncio .anuncio-content {
  padding: 2rem;
}
.anuncio h3,
.anuncio p {
  margin: 0;
}

.price {
  color: #71b100;
  font-weight: 700;
  font-size: 3rem;
}

.icons-characteristics {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.icons-characteristics li {
  flex: 1;
  display: flex;
}
.icons-characteristics li img {
  flex: 0 0 5rem;
  margin-right: 1rem;
}
.icons-characteristics li p {
  font-weight: 900;
}

.summary-property {
  padding: 2rem;
}

.no-webp .contact-image {
  background-image: url("../img/encuentra.jpg");
}

.webp .contact-image {
  background-image: url("../../build/img/encuentra.webp");
}

.contact-image {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact-image h2 {
  font-size: 4rem;
  font-size: 900;
  color: #ffffff;
}
.contact-image p {
  font-size: 1.8rem;
  color: #ffffff;
}

@media (min-width: 768px) {
  .bottom-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;
  }
}
.input-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .input-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 2rem;
  }
}
.input-blog:last-of-type {
  margin-bottom: 0;
}

.input-text a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .input-text a {
    margin-top: 0;
  }
}
.input-text h4 {
  margin: 0;
  line-height: 1.4;
}
.input-text h4::after {
  content: "";
  margin-top: 1rem;
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71b100;
}

.meta-information span {
  color: #e08709;
}

.testimonial {
  background-color: #71b100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #ffffff;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url("../img/comilla.svg");
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #ffffff;
}

.form p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.form legend {
  font-size: 2rem;
  color: #333333;
}
.form label {
  margin: 1rem 0;
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #ffffff;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.form textarea {
  height: 20rem;
}
.form .contact-form {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form input[type=radio] {
  margin: 0;
  width: auto;
}

.footer {
  padding: 2.5rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .container-footer {
  padding-top: 2.5rem;
}
.footer .navigation {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #ffffff;
  text-align: center;
}

/**
*?  Paginas Internas
**/
.us {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .us {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.text-content h4 {
  font-weight: 900;
}

.alert {
  padding: 0.5rem;
  text-align: center;
  color: #ffffff;
  font-weight: bold;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alert.error {
  background-color: #a50000;
}
.alert.success {
  background-color: #71b100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #71b100;
}
table.propiedades thead th {
  color: #ffffff;
  padding: 2rem;
}
table.propiedades .table-image {
  width: 10rem;
  border-radius: 1rem;
}

.small-image {
  width: 30rem;
  border-radius: 1rem;
}