@include tablet {
	.bottom-section {
		display: grid;
		grid-template-columns: 2fr 1fr;
		gap: 2rem;
	}
}

.input-blog {
	margin-bottom: 2rem;

	@include tablet {
		display: grid;
		grid-template-columns: 1fr 2fr;
		gap: 2rem;
	}

	&:last-of-type {
		margin-bottom: 0;
	}
}

.input-text {
	a {
		color: $black;
		margin-top: 2rem;
		display: block;

		@include tablet {
			margin-top: 0;
		}
	}

	h4 {
		margin: 0;
		line-height: 1.4;

		&::after {
			content: '';
			margin-top: 1rem;
			display: block;
			width: 15rem;
			height: 0.5rem;
			background-color: $green;
		}
	}
}

.meta-information {
	span {
		color: $yellow;
	}
}
